"use client";

import { useVote } from "@/context/VoteContext";

const candidates = ["Tami Suastuti", "Intan Oka Apriliani", "Merthi Utami"];

export default function ResultsPage() {
  const { voteCounts } = useVote();

  return (
    <div className="max-w-xl mx-auto p-6">
      <h1 className="text-3xl font-bold mb-6">Election Results</h1>

      <ul className="list-disc pl-6 space-y-2">
        {candidates.map((candidate) => (
          <li key={candidate} className="text-lg">
            {candidate}: {voteCounts[candidate] || 0} votes
          </li>
        ))}
      </ul>
    </div>
  );
}
